#popup {
  font-size: 0.85rem;
  display: none;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 60%;
  max-width: 400px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  background-color: white;
  z-index: 1000;
  text-align: center;
  font-family: 'Raleway', sans-serif;
  font-weight: 500;
}

div#popup button {
  display: block;
  margin: 20px auto 0;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #5abbe8;
  color: white;
  font-size: 1em;
  cursor: pointer;
}

div#popup button:hover {
  background-color: #4592b5;
}

@media only screen and (max-width: 1024px) {
  #popup {
    display: block;
  }
}

