/*wave animation*/
.waves {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  z-index: 2;
}

.waves h1 {
  font-family: "Saira Stencil One", sans-serif;
  font-weight: 400;
  font-style: normal;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  font-size: 9em;
  color: rgba(255, 255, 255);
  text-shadow: 2px 2px 50px#5abbe8;
}

.waves h2 {
  font-family: "Saira Stencil One", sans-serif;
  font-weight: 400;
  font-style: normal;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  font-size: 9em;
  color: rgba(255, 255, 255, .1);
  background-image: url(img/fishiewaves.png);
  background-repeat: repeat-x;
  background-clip: text;
  animation: animate 15s linear infinite;
}

@keyframes animate {
  0% {
    background-position: left 0px top 80px;
  }

  40% {
    background-position: left 800px top -10px;
  }

  80% {
    background-position: left 1800px top -10px;
  }

  100% {
    background-position: left 2400px top 80px;
  }
}

/*water ripples*/
#caustics {
  position: absolute;
  bottom: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  filter: url(#noise1);
}

#caustics::before,
#caustics::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-image: url("img/caustics.jpeg");
  background-repeat: repeat;

  --duration: 10s;
  --gapY: 0px;

  background-size: calc(100vw / var(--ratioW)) 30vh;
  animation: caustics calc(var(--duration) * (var(--ratioW))) linear infinite;
  opacity: 0.1;
  mask-image: linear-gradient(to top,
      white,
      transparent,
      transparent);
}

#caustics::after {
  --duration: 11s;
  --gapY: 10vh;
  animation-delay: -2s;
  transform: scale3d(-1, 1, 1);
}

@keyframes caustics {
  0% {
    background-position: bottom var(--gapY) left;
  }

  100% {
    background-position: bottom var(--gapY) left -100vw;
  }
}

@media (orientation: portrait) {
  :root {
    --ratioW: 1;
  }
}

@media (min-aspect-ratio: 1/1) {
  :root {
    --ratioW: 1;
  }
}

@media (min-aspect-ratio: 2/1) {
  :root {
    --ratioW: 2;
  }
}

@media (min-aspect-ratio: 3/1) {
  :root {
    --ratioW: 3;
  }
}

@media (min-aspect-ratio: 4/1) {
  :root {
    --ratioW: 4;
  }
}

@media (min-aspect-ratio: 5/1) {
  :root {
    --ratioW: 5;
  }
}

